"use client"

import React, { useState, useRef, useEffect } from 'react';
import { User, Calendar, Clock, Phone, MapPin, Save } from 'lucide-react';

// Google Places script load කරන function එක
const loadGooglePlacesScript = (callback: () => void) => {
  if (document.getElementById('google-places-script')) {
    callback();
    return;
  }

  const script = document.createElement('script');
  script.id = 'google-places-script';
  script.src = `https://maps.googleapis.com/maps/api/js?key=YOUR_API_KEY&libraries=places`;
  script.async = true;
  script.defer = true;
  script.onload = callback;
  document.body.appendChild(script);
};

export default function ProfilePage() {
  const [formData, setFormData] = useState({
    gender: '',
    birthTime: '',
    dateOfBirth: '',
    whatsappNumber: '',
    birthLocation: '',
    latitude: '',
    longitude: ''
  });

  const autocompleteRef = useRef<any>(null);
  const inputRef = useRef<HTMLInputElement>(null);

  useEffect(() => {
    // Google script load වෙලා තියෙනවද කියලා බලලා autocomplete init කරනවා
    loadGooglePlacesScript(() => {
      if (!window.google || !inputRef.current) return;

      const autocomplete = new window.google.maps.places.Autocomplete(inputRef.current, {
        types: ['geocode'], // cities, establishments, etc.
        fields: ['formatted_address', 'geometry', 'name']
      });

      autocomplete.addListener('place_changed', () => {
        const place = autocomplete.getPlace();

        if (!place.geometry || !place.geometry.location) {
          alert('No details available for input: ' + place.name);
          return;
        }

        const lat = place.geometry.location.lat();
        const lng = place.geometry.location.lng();

        setFormData(prev => ({
          ...prev,
          birthLocation: place.formatted_address || place.name || '',
          latitude: lat.toFixed(6),
          longitude: lng.toFixed(6)
        }));
      });

      autocompleteRef.current = autocomplete;
    });
  }, []);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = () => {
    console.log('Profile Data:', formData);
    alert('Profile updated successfully!');
  };

  return (
    <div className="min-h-screen bg-white p-6">
      <div className="max-w-2xl mx-auto">
        {/* Header */}
        <div className="text-center mb-8">
          <div className="inline-flex items-center justify-center w-20 h-20 bg-blue-600 rounded-full mb-4">
            <User className="w-10 h-10 text-white" />
          </div>
          <h1 className="text-3xl font-bold text-black mb-2">Profile</h1>
          <p className="text-gray-600">Update your personal information</p>
        </div>

        <div className="space-y-6">
          {/* Gender */}
          <div>
            <label className="block text-black font-semibold mb-2">Gender</label>
            <select
              name="gender"
              value={formData.gender}
              onChange={handleInputChange}
              className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:border-blue-600 focus:outline-none text-black"
            >
              <option value="">Select Gender</option>
              <option value="male">Male</option>
              <option value="female">Female</option>
              <option value="other">Other</option>
            </select>
          </div>

          {/* Date of Birth */}
          <div>
            <label className="block text-black font-semibold mb-2 flex items-center gap-2">
              <Calendar className="w-5 h-5 text-blue-600" />
              Date of Birth
            </label>
            <input
              type="date"
              name="dateOfBirth"
              value={formData.dateOfBirth}
              onChange={handleInputChange}
              className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:border-blue-600 focus:outline-none text-black"
            />
          </div>

          {/* Birth Time */}
          <div>
            <label className="block text-black font-semibold mb-2 flex items-center gap-2">
              <Clock className="w-5 h-5 text-blue-600" />
              Birth Time
            </label>
            <input
              type="time"
              name="birthTime"
              value={formData.birthTime}
              onChange={handleInputChange}
              className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:border-blue-600 focus:outline-none text-black"
            />
          </div>

          {/* WhatsApp Number */}
          <div>
            <label className="block text-black font-semibold mb-2 flex items-center gap-2">
              <Phone className="w-5 h-5 text-blue-600" />
              WhatsApp Number
            </label>
            <input
              type="tel"
              name="whatsappNumber"
              value={formData.whatsappNumber}
              onChange={handleInputChange}
              placeholder="+94 77 123 4567"
              className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:border-blue-600 focus:outline-none text-black"
            />
          </div>

          {/* Birth Location with Google Places Autocomplete */}
          <div>
            <label className="block text-black font-semibold mb-2 flex items-center gap-2">
              <MapPin className="w-5 h-5 text-blue-600" />
              Birth Location
            </label>
            
            <input
              ref={inputRef}
              type="text"
              placeholder="Type your birth city or place (e.g. Colombo, Kandy, London...)"
              className="w-full px-4 py-3 border-2 border-gray-300 rounded-lg focus:border-blue-600 focus:outline-none text-black"
            />

            {/* Selected Location Display */}
            {formData.birthLocation && (
              <div className="mt-4 bg-blue-50 border-2 border-blue-200 rounded-lg p-4 space-y-3">
                <p className="text-black font-semibold">Selected Birth Location:</p>
                <p className="text-gray-800">{formData.birthLocation}</p>
                <div className="grid grid-cols-2 gap-4 text-sm">
                  <div>
                    <span className="text-gray-600">Latitude:</span>
                    <code className="ml-2 text-black font-mono bg-white px-2 py-1 rounded">
                      {formData.latitude}
                    </code>
                  </div>
                  <div>
                    <span className="text-gray-600">Longitude:</span>
                    <code className="ml-2 text-black font-mono bg-white px-2 py-1 rounded">
                      {formData.longitude}
                    </code>
                  </div>
                </div>
              </div>
            )}
          </div>

          {/* Submit Button */}
          <button
            onClick={handleSubmit}
            className="w-full bg-blue-600 text-white font-bold py-4 rounded-lg hover:bg-blue-700 transition-colors flex items-center justify-center gap-2 shadow-lg"
          >
            <Save className="w-5 h-5" />
            Update Profile
          </button>
        </div>

        {/* Instructions */}
        <div className="mt-8 text-center text-xs text-gray-500">
          Powered by Google Places API • Make sure to add your own API key
        </div>
      </div>
    </div>
  );
}